.main {
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main h2, .main h3 {
  font-family: HeaderFont;
}
.main h2 {
  font-size: 5vw;
}
.main h3 {
  font-size: 3vw;
  text-align: center;
}
.main h3 span {
  font-size: 2vw;
}
.main nav {
  width: 60vw;
  display: flex;
  justify-content: space-between;
}
.main nav button {
  background-color: #24153b;
  color: #814bd2;
  border: none;
  border-radius: 2vw;
  box-shadow: 4px 4px 4px black;
  padding: 1vw;
  font-size: 2vw;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}